import React, { Component } from 'react';

class TOC extends Component {
  // í˜„ì¬ ë°ì´í„°ì™€ ìƒˆë¡œìš´ ë°ì´í„°ê°€ ê°™ë‹¤ë©´ renderí•¨ìˆ˜ë¥¼ í˜¸ì¶œí•˜ì§€ ì•ŠëŠ”ë‹¤.(ì„±ëŠ¥ê°œì„ )
  // https://ko.reactjs.org/docs/react-component.html#shouldcomponentupdate
  // ê¸°ë³¸ ë™ì‘ì€ ë§¤ state ë³€í™”ë§ˆë‹¤ ë‹¤ì‹œ ë Œë”ë§ì„ ìˆ˜í–‰í•˜ëŠ” ê²ƒì´ë©°, ëŒ€ë¶€ë¶„ì˜ ê²½ìš° ê¸°ë³¸ ë™ì‘ì— ë”°ë¼ì•¼ í•œë‹¤.
  // props ë˜ëŠ” stateê°€ ìƒˆë¡œìš´ ê°’ìœ¼ë¡œ ê°±ì‹ ë˜ì–´ì„œ ë Œë”ë§ì´ ë°œìƒí•˜ê¸° ì§ì „ì— í˜¸ì¶œëœë‹¤.
  shouldComponentUpdate(newProps) {
    if (this.props.data === newProps.data) {
      return false;
    }
    return true;
  }
  render() {
    let lists = [];
    let data = this.props.data;
    let i = 0;
    while (i < data.length) {
      lists.push(
        <li key={data[i].id}>
          <a
            onClick={function (e) {
              e.preventDefault();
              this.props.onChangePage(e.target.dataset.id);
            }.bind(this)}
            href={'/content/' + data[i].id}
            data-id={data[i].id}
          >
            {data[i].title}
          </a>
        </li>
      );
      i = i + 1;
    }
    return (
      <nav>
        <ul>{lists}</ul>
      </nav>
    );
  }
}

export default TOC;
